import React from 'react';
import Standalone from '../layouts/standalone';
import SEO from '../seo';
import ListingSummary from '../listing-summary';
import { graphql} from 'gatsby';

const ListingList = (props) => {
  const { type, name, tag } = props.data.thisCategory.frontmatter;
  const { commonKeywords } = props.data.site.siteMetadata;
  const listings = props.data.listings.edges.map(edge => edge.node);
  const allCategories = props.data.categories.edges.map(edge => edge.node);
  return (
    <Standalone>
      <SEO
        title={ `Expatty ${ type === 'category' ? 'category' : 'areas'} | ${name}` }
        description={ `Attractions, events, businesses and services in Andalusia, Spain, related to: ${name}` }
        keywords={[name, ...commonKeywords]}/>
      <div className="container-fluid">
        <div className="row">
          <div className="col-md">
            <main>
              <h1 className="title--header">{ name }</h1>
              {
                listings
                  .filter(({ frontmatter }) => frontmatter.tags.indexOf(tag) >= 0)
                  .map((node, i) => {
                    const listingIcons =
                      node.frontmatter.tags
                        .map(tg => allCategories.find(catNode => catNode.frontmatter.tag === tg))
                        .filter(catNode => catNode)
                        .map(catNode => ({
                          icon: catNode.frontmatter.icon,
                          name: catNode.frontmatter.name,
                          path: catNode.fields.slug
                        }));
                    return (
                    <ListingSummary
                      key={ i }
                      description={ node.frontmatter.description }
                      title={ node.frontmatter.title }
                      url={ node.fields.slug }
                      icons={ listingIcons }/>
                  )
                })
              }
            </main>
          </div>
        </div>
      </div>
    </Standalone>
  );
};

export default ListingList;

export const pageQuery = graphql`
    query CategoryOrArea($type: String!, $name: String!) {
        site {
            siteMetadata {
                commonKeywords
            }
        }
        thisCategory: markdownRemark(frontmatter: { type: { eq: $type }, name: { eq: $name } }) {
            frontmatter {
                type
                name
                tag
            }
        }
        listings: allMarkdownRemark(filter: {frontmatter: {type: {eq: "listing"}}}) {
            edges {
                node {
                    frontmatter {
                        title
                        description
                        tags
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
`